* {
    justify-content: center;
    align-items: center;
}

div {
    justify-content: center;
    margin: 8px 0;
    display: inline-block;
    box-sizing: border-box;
    padding-left: 30%;
    padding-top: 10%;
    align-items: center;
}

h1 {
    height: 100px;
    font-size: 60px;
    align-items: center;
    text-decoration: solid;
    text-align: center;
}

h6 span {
    height: 90px;
    font-size: 10px;
    text-align: center;
    color: #ccc;
}

button {
    background-color: #924c9a;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    cursor: pointer;
    width: 100%;
    border-radius: 10px;
}